* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  height: 100%;
  width: 100%;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#preview {
  width: 600px;
  display: block;
  margin: auto;
  height: 300px;
  z-index: 100;
  position: relative;
  border: 1px solid black;
  background-color: whitesmoke;
  overflow: scroll;
  padding-left: 1px;
  white-space: nowrap;
}

#editor {
  width: 600px;
  display: block;
  margin: auto;
  margin-bottom: 30px;
  height: 300px;
  z-index: 100;
  position: relative;
  padding-left: 2px;
  padding-top: 2px;
}
.headers {
  width: 600px;
  margin: auto;
  text-align: left;
  border: 1px solid black;
  margin-bottom: -1px;
  padding: 1px;
}

@media (max-width: 1000px) {
  #preview {
    width: 60vw;
    font-size: 12px;
  }
  #editor {
    width: 60vw;
    font-size: 12px;
  }
  .headers {
    width: 60vw;
    font-size: 12px;
  }
}
h1 {
  font-size: x-large;
  margin-bottom: 15px;
}
